<template>
  <div class="lease-container">
    <div id="leaseMap" class="lease-map"></div>
    <div class="lease-control">
      <button class="lease-control-item lease-button"
        :class ="autofocusFlag ? 'default-focus' : ''">租車
      </button>
      <button class="lease-control-item return-button" @click="test">還車</button>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.lease-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.lease-control {
  position: absolute;
  top: 36px;
  left: calc(50% - 76px);
  width: 134px;
  height: 42px;
  z-index: 2;
  &-item {
    height: 100%;
    width: 50%;
    border: none;
  }
}
.lease-map {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.lease-button {
  background-color: $primary;
  color: $secondary;
  border-radius: 10px 0 0 10px;
  &:hover {
    outline: $secondary solid 5px;
  }
  &:focus {
    outline: $secondary solid 5px;
  }
}
.return-button {
  background-color: $secondary;
  color: $light;
  border-radius: 0px 10px 10px 0px;
  &:hover {
    outline: $primary solid 5px;
  }
  &:focus {
    outline: $primary solid 5px;
  }
}
.default-focus {
  outline: $secondary solid 5px;
}
</style>

<script>
import L from 'leaflet';
import { getLocationData } from '../assets/js/apiDataProcess';

export default {
  data() {
    return {
      mapObject: {},
      mapLayer: {},
      locationLog: '',
      locationLat: '',
      autofocusFlag: true,
      markerGroup: {},
      leaseMarkar: {
        iconUrl: '@/assets/images/png/blue_kuma_markar.png',
        iconSize: [58, 56],
        iconAnchor: [22, 94],
        popupAnchor: [-3, -76],
      },
      leaseData: [],
    };
  },
  watch: {
    leaseData(data) {
      console.log(data);
      data.forEach((item) => {
        console.log(item.StationPosition.PositionLat, item.StationPosition.PositionLon);
        this.markerGroup.addLayer(
          L.marker(
            [item.StationPosition.PositionLat, item.StationPosition.PositionLon],
            { icon: L.icon(this.leaseMarkar) },
          ),
        ).addTo(this.mapObject);
      });
    },
  },
  methods: {
    createMap() {
      this.mapObject = L.map('leaseMap', {
        center: [22.6561314, 120.2984205],
        zoom: 14,
      });
      this.mapLayer = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png').addTo(this.mapObject);
      this.markerGroup = L.layerGroup();
    },
    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const { longitude, latitude } = position.coords;
            this.locationLog = longitude;
            this.locationLat = latitude;
            // const longitude = position.coords.longitude; // 經度
            // const latitude = position.coords.latitude; // 緯度
            // console.log(this.locationLog);
            console.log('fwr', latitude);
            // 重新設定 view 的位置
            this.setMap(this.locationLog, this.locationLat);
            this.getLocationDate(this.locationLog, this.locationLat);
            // mymap.setView([latitude, longitude], 13);
            // 將經緯度當作參數傳給 getData 執行
            // getStationData(longitude, latitude);
          },
          (error) => {
            /* 錯誤訊息 */
            const { code, message } = error;
            // const msg = e.code;
            // const dd = e.message;
            console.error(code);
            console.error(message);
          },
        );
      }
    },
    setMap(log, lat) {
      console.log('dd', log, lat);
      this.mapObject.setView([lat, log], 13);
    },
    defaultSet() {
      this.autofocusFlag = true;
    },
    async getLocationDate(long, lat) {
      this.leaseData = await getLocationData(lat, long);
    },
    test() {
      this.autofocusFlag = false;
    },
  },
  mounted() {
    this.createMap();
    this.getLocation();
    this.defaultSet();
  },
};
</script>
